@import "../../../node_modules/react-alice-carousel/lib/alice-carousel.css";
.aws-btn {
    --slider-height-percentage: 30%;
    --slider-transition-duration: 387ms;
    --organic-arrow-thickness: 4px;
    --organic-arrow-border-radius: 0px;
    --organic-arrow-height: 18px;
    --organic-arrow-color: #cecece;
    --control-button-width: 10%;
    --control-button-height: 25%;
    --control-button-background: transparent;
    --control-bullet-color: #cecece;
    --control-bullet-active-color: #747474;
    --loader-bar-color: transparent;
    --loader-bar-height: 6px;
}
.aws-sld__bullets button{
    width: 6px;
    height: 6px;
    display: none;
}
.advantage-color{
    color:#9D9D9D;
}
/* Experiencias */
.experience-container-carousel .alice-carousel{
    background-color: #4D5766;
}
.experience-container-carousel .alice-carousel__stage-item{
    padding-left: unset;
    padding-right: unset;
    margin-bottom: unset;
}
.experience-container-carousel .alice-carousel__dots{
    margin: 0px;
    height: 30px;
}
.luxury-experience{
    background: linear-gradient(rgba(0, 0, 0, 0.45),rgba(0, 0, 0, 0.45)), url('../images/experiencias-luxury.jpg');
    background-repeat: no-repeat;
    background-origin: inherit;
    background-size: cover;
}
.luxury-experience h6, .luxury-experience span{
    font-weight: lighter;
    font-size: 31px;
    color:white
}
.luxury-experience span{
    font-weight: bold;
}
.experience-background{
    background-color: #4D5766;
}
.img-experience{
    width: 90px;
    height: 90px;
}
.img-experience img{
    border-radius: 50%;
}
.experience-title{
    font-size: 21px;
    font-weight: lighter;
}
.experience-type{
    font-size: 14px;
}
.experience-description{
    font-size: 14px;
    font-weight: lighter;
}
/* Treatments */
.treatments-background{
    background-color: #F2F2F2;
}
.card-container{
    background-color: white;
    border-radius: 9px;
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
    height: 450px;
}
.alice-carousel__stage-item{
    padding-left: 15px;
    padding-right: 15px;
    margin-bottom: 10px;
}
.card-image img{
    border-radius: 9px 9px 0 0;
}
.treatments-title, .especialist-title, .contact-title{
    color:#4D5766;
    font-family: 'Roboto',sans-serif;
    font-weight: lighter;
    font-size: 27px;
}
.treatments-container{
    padding: 0px;
    width: 80%;
    margin: 0px;
    align-self: center;  
}
.treatment-description-container{
    height: 73px;
}
.treatment-description{
    color:#9D9D9D;
    font-size: 14px;
    text-align: justify;
}
.cards-title{
    color:#4D5766;
    font-weight: lighter;
    font-size: 23px;
}
.cards-carousel > div:first-child{
    padding-bottom: 20px;
}
.card-date-button {
    font-size: 12px;
    background-color: #69ABBE;
    border-radius: 19px;
    color:white;
}
.read-more-card{
    font-size: 12px;
}
.custom-chevron{
    background-color: #d1d1d15c;
    box-shadow: none;
    color: #848484;
    width: 50px;
    height: 50px;
    padding: 17px;
    border-radius: 50%;
}
.custom-chevron.experience{
    color:#f1f1f1;
}
.see-all{
    background-color: white;
    border-radius: 24px;
    box-shadow: #31313129 0px 3px 6px;
}
/* Especialist */
.especialist-info, .especialist-image{
    height: 200px;
}
.especialist-image{

}
.especialist-image img{
    border-radius: 50%;
    border: 1px solid #BCBCBC;
}
.especialist-name{
    color:#4D5766;
    font-weight: lighter;
    font-size: 23px;
    margin-bottom: 0;
}
.especialist-prefix{
    font-weight: bold;
    font-size: 23px;
}
.especialist-school{
    color:#9D9D9D;
    font-size: 14px;
    font-weight: lighter;
    margin-top: 8px;
    margin-bottom: 4px;
}
.especialist-treatment{
    color: #69ABBE;
    font-weight: 500;
    font-size: 19px;
    margin-top: 4px;
}
.especialist-read-more{
    font-weight: lighter;
    font-size: 12px;
    text-transform:uppercase;
    color: #69ABBE;
}
.quality-icon svg{
    width: 35px;
}

.blue-container{
    background-color:rgba(56, 170, 221, 0.16);
    color:#9D9D9D;
    font-size: 14px;
    text-align: left;
    border-radius: 6px;
}
.custom-border{
    border-right: 1px solid lightgrey;
}
.custom-border p{
    margin-top: 5px;
    margin-bottom: 5px;
}
.top-border{
    border-top: 1px solid lightgray;
}
.see-route-link svg{
    width: 25px;
    height: 25px;
}
.embebed-map iframe{
    width: 100%;
    height: -webkit-fill-available;
    border:none;
    border: 2px solid #E0E0E0;
    border-radius: 6px;
    box-sizing: border-box;
}
.map-container{
}
.ofix-container img{
}
.mobile-card {
    box-shadow: 0px 3px 6px #00000029;
    border-radius: 9px;
    margin-right: 27px;
    margin-left: 27px;
    padding: 15px;
}
.contact-mobile{
    display: none;
}
@media (max-width: 960px) {
    .advantages-container{
        overflow: auto;
        white-space: nowrap;
        justify-content: unset;
    }
    .advantage-container{
        width: 300px;
    }
    .advantages-container::-webkit-scrollbar {
        display: none;
    }
    .treatments-background{
        padding-left: 1rem;
        padding-right: 1rem;
    }
    .experience-container{
        flex-direction: column; 
        margin-bottom: 120px;
    }
    
    .experience-container > div{
        width: 100%;
        height: 300px;
    }
    .luxury-experience{
        background-size: auto;
        background-position: top;
    }
    /* .especialist-container > div{
        width: 100%;
        padding-left: 10%;
        display: flex;
        white-space: nowrap;
        overflow: auto;
        padding: 30px;
    } */
    .especialist-container{
        display: inline-block;
        justify-content: unset;
    }
    .ofix-contact-info{
        width: 98%
    }
    .contact-desktop{
        display: none;
    }
    .contact-mobile{
        display: block
    }
    .route-links{
        flex-direction: column;
    }
    .route-links a{
        margin-top: 20px;
    }
    .custom-border{
        border:none;
    }
    .custom-border ~ div{
        padding: 0;
    }
    .top-border{
        padding-top: 20px;
        padding-bottom: 20px;
    }
    .aws-sld__wrapper{
        height: 200px;
    }
    .especialist-card-mobile{
        border-radius: 9px;
        box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
    }
    .treatments-container{
        width: 100%;
    }

}

